<template>
  <h3>Teams</h3>
  <div class="d-flex">
    <button class="btn btn-sm btn-primary" @click="balance">Balance</button>
    <div class="form-file form-file-sm wf">
      <input type="file" id="importOldFile" class="form-file-input d-none" @change="imp" />
      <label for="importOldFile" class="form-file-label w-100">
        <span class="form-file-button">Import Old</span>
      </label>
    </div>
    <button class="btn btn-sm btn-danger ml-2" @click="clear">Clear</button>
    <span>Dispersion value: {{ range }}</span>
    <input
      type="range"
      style="width:250px;"
      class="form-range"
      min="0"
      max="250"
      step="1"
      v-model="range"
    />
  </div>
  <div class="teams pb-5 mb-5">
    <team v-for="team in teams" :key="team.name" :team="team" />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from 'vue';
import { useStore } from '@/store';
import MutationTypes from '@/store/mutation-types';

import Team from '@/components/Teams/Team.vue';

const wasm = import('@/../pkg/index.js');

export default defineComponent({
  name: 'Teams',
  components: { Team },
  setup() {
    const store = useStore();
    const storeTeams = computed(() => store.state.teams);
    const range = ref(0);

    const balance = () => {
      wasm
        .then((lib) => {
          try {
            const { leftovers, teams } = lib.balance(
              store.state.players,
              +range.value
            );
            const ignoredUuids = leftovers.reduce((acc, leftover) => {
              acc.push(leftover.uuid);
              return acc;
            }, []);

            store.commit(MutationTypes.RESERVE_PLAYERS, ignoredUuids);
            store.commit(MutationTypes.ADD_TEAMS, teams);

            console.log('Received: ', leftovers, teams);
          } catch (e) {
            console.error(e.message);
          }
        })
        .catch((e) => {
          console.error(e.message);
        });
    };

    const clear = () => {
      store.commit(MutationTypes.CLEAR_TEAMS);
    };

    const onReaderLoad = (event: ProgressEvent<FileReader>) => {
      if (!event.target) return;

      const data = event.target.result as string;
      store.commit(MutationTypes.IMPORT_PLAYERS_OLD, data);
    };

    const imp = (event: Event) => {
      const reader = new FileReader();
      const { files } = event.target as HTMLInputElement;

      if (files !== null && files.length) {
        reader.onload = onReaderLoad;
        reader.readAsText(files[0]);
      }

      console.log('Hi! 2');
    };

    return { balance, range, clear, teams: storeTeams, imp };
  },
});
</script>

<style lang="scss" scoped>
.teams {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: auto auto auto;
  column-gap: 2rem;
  row-gap: 2rem;
  margin-top: 2rem;
  max-width: 1024px;
}
.wf {
  width: 6rem;
}
</style>
