
import { defineComponent, watch, onMounted, ref } from 'vue';

import Modal from 'bootstrap/js/src/modal';

export default defineComponent({
  name: 'Modal',
  props: {
    title: String,
    isActive: Boolean,
  },
  setup(props, { emit }) {
    const modalRef = ref(null);

    const setIsActive: (
      modal: Modal,
      isActive: boolean,
      isActiveP: boolean
    ) => void = (modal, isActive, isActiveP) => {
      if (isActive === isActiveP) {
        return;
      }

      if (isActive) {
        modal.show();
        return;
      }

      modal.hide();
    };

    let modal: null | Modal = null;
    onMounted(() => {
      modal = new Modal(modalRef.value);

      ((modalRef.value as unknown) as Element).addEventListener(
        'hidden.bs.modal',
        () => {
          emit('close-modal');
        }
      );

      setIsActive(modal, props.isActive || false, !props.isActive);
    });

    watch(
      () => props.isActive,
      (isActive, isActiveP) => setIsActive(modal, !!isActive, !!isActiveP)
    );

    return {
      modalRef,
    };
  },
});
